.Home{
    display: flex;
    flex-direction: column;
    align-items: center;
    scroll-behavior: auto;
    scroll-snap-type: both mandatory ;
    overflow: scroll;
    height: 100vh;
    -ms-overflow-style: none;
    scrollbar-width: none;
    background-color: black;
}

.Home::-webkit-scrollbar {
    display: none;
  }

.Banner{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 105vh;
    background-color: black;
    overflow: hidden;
    scroll-snap-align: start;
    flex-shrink: 0;
}

.BannerImg{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    background-image: url("./images/Home.jpg");
    background-position: center;
    background-size: cover;
    flex-shrink: 0;
}

.BannerText{
    display: flex;
    transform-origin: center;
    width: 100%;
    height:39%;
    background-image: url("./images/Title.png");
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    flex-shrink: 0;
}

.ButtonContainer{
    display: flex;
    flex-direction: row;
    gap: 3vw;
    align-self: center;
}

.ArrowImg{
animation-name: arrowAnim;
animation-duration: 1s;
animation-timing-function: ease-in-out;
animation-direction: alternate-reverse;
animation-iteration-count: infinite;
}

@keyframes arrowAnim {
    0% { transform: translateY( 0vh ); }
    100% { transform: translateY( 1vh ); }
}

.AboutWorlds{
    display: flex;
    flex-direction: row;
    align-items: start;
    justify-content: start;
    width: 100%;
    height: 105vh;
    background-image: linear-gradient( to bottom right , #6195a6 , #0a2943 ); 
    overflow: hidden;
    scroll-snap-align: start;
    flex-shrink: 0;
}

.AboutText{
    display: flex;
    transform-origin: center;
    width: 40%;
    height:25%;
    background-image: url("./images/About.png");
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    flex-shrink: 0;
    margin-top: 3vh;
    margin-left: -3vw;
    align-self: start;
}

.AboutDivider{
    display: flex;
    width: 50%;
    height: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

h2{
    color: white;
    align-self: start;
    font-weight: normal;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    width: 75%;
    word-wrap: break-word;   
    margin-left: 0vw;
    margin-top: 0;
    font-size: 2.5vh;
    text-align: left;
}

.TreeImg{
    display: flex;
    position: relative;
    height: 66%;
    aspect-ratio: 1;
    bottom: 0;
    right: 0;
    border-radius: 0.5cm;
    
    animation-name: TreeAnim;
    animation-duration: 2s;
    animation-timing-function: ease-in-out;
    animation-direction: alternate-reverse;
    animation-iteration-count: infinite;

}

@keyframes TreeAnim {
    0% { transform: translateY(0%); }
    100% { transform: translateY(5%); }
}

.GptTest{
    display: flex;
    flex-direction: row;
    align-items: start;
    justify-content: start;
    width: 100%;
    height: 105vh;
    background-image: linear-gradient( to bottom right , #4a4a4a , #090909 ); 
    overflow: hidden;
    scroll-snap-align: start;
    flex-shrink: 0;
}

.MapGen{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 105vh;
    background-image: url("./images/mapgen.gif");
    background-size: cover;
    background-position: center;
    overflow: hidden;
    scroll-snap-align: end;
    flex-shrink: 0;
    gap:0;
}

.MapGenDarken{
    display: flex;
    flex-direction: column;
    justify-content: end;
    align-items: center;
    background-image: linear-gradient(to bottom , #00000000 , #000000AA , #000000FF);
    height: 110vh;
    width: 100vw;
    position: static;
    top:35%;
    left:0;
    flex-shrink: 0;
}

.MapGenText{
    display: flexbox;
    position: relative;
    width: 39%;
    height:39%;
    background-image: url("./images/ProcGenCenter.png");
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    flex-shrink: 0;
    border-radius: 0.5cm;
    margin-bottom: -5%;
    margin-top: 0;
}


h1{
    color: #EEE;
    font-weight: bold;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    width: 90%;
    word-wrap: break-word;   
    margin-top: 0;
    font-size: 3vh;
    text-align: center;
    padding: 5vh;
    border-radius: 0.5cm;
    margin-bottom: 5vh;
}